.main-navigation-wrapper {
  background-color: rgba(235, 235, 235, 0.25);
  background-image: linear-gradient(to bottom, #1f1f1f, #2b2b2b 12%, #404040 30%, #4d4d4d 50%, #404040 75%, #1f1f1f);
  position: relative;
  padding-left: 3em;
}

.main-navigation {
  clear: both;
  transition: all .3s;
}

@media all and (min-width: 34.375em) {
  .main-navigation {
    padding: 0;
  }
}

.main-navigation-wrapper::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 0;
}

.main-navigation-wrapper::after {
  clear: both;
  content: "";
  display: table;
}

.main-navigation-wrapper > .main-navigation,
.main-navigation > .nav-menu,
.main-navigation > .nav-menu > .menu-item {
  float: left;
}

.main-navigation > .nav-menu {
  list-style: none;
  min-height: 40px;
}

.main-navigation > .nav-menu > .menu-item {
  font-size: .875em;
  margin-right: 2px;
  letter-spacing: 1px;
  word-spacing: -1px;
}

.main-navigation a {
  position: relative;
  display: block;
  padding-top: .8em;
  padding-bottom: .559em;
  line-height: 1.5;
}

[data-okay-overflow] a,
.nav-menu a {
  text-decoration: none;
  color: #fff;
}

.nav-menu a {
  padding-right: 1em;
  padding-left: 1em;
  border-bottom-left-radius: 12px 8px;
  border-bottom-right-radius: 12px 8px;
}

.index .menu-item-index a,
.nav-menu .menu-item-current a {
  pointer-events: none;
  cursor: default;
  color: #5eed8e;
}

.index .menu-item-index a,
.nav-menu .menu-item-current a,
.nav-menu a:hover,
.nav-menu a:active {
  background-color: #1f1f1f;
}

.nav-menu a:hover,
.nav-menu a:active {
  color: white;
}

.nav-menu a span {
  position: relative;
  z-index: 2;
}

[data-okay-toggle] {
  border: none;
  float: right;
  outline-offset: -0.25em;
  margin-left: 1em;
  padding: .4em 1em .35em 1.75em;
  position: relative;
  top: 2px;
  background-color: #999999;
  background-image: url("data:image/svg+xml;utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink' width='48' height='36' viewBox='0 0 48 36' fill='rgb(41,61,48)'%3E%3Crect x='8' y='11' width='16' height='2' /%3E%3Crect x='8' y='16' width='16' height='2' /%3E%3Crect x='8' y='21' width='16' height='2' /%3E%3C/svg>");
  background-position: 0 0;
  background-repeat: no-repeat;
  color: #ebebeb;
  cursor: pointer;
}

@media all and (min-width: 34.375em) {
  [data-okay-toggle] {
    padding-top: .25em;
    padding-bottom: .3em;
  }
}

[data-okay-toggle]:hover,
[data-okay-toggle][aria-expanded="true"] {
  background-color: #8c8c8c;
  background-image: url("data:image/svg+xml;utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink' width='48' height='36' viewBox='0 0 48 36' fill='rgb(82, 224, 129)'%3E%3Crect x='8' y='11' width='16' height='2' /%3E%3Crect x='8' y='16' width='16' height='2' /%3E%3Crect x='8' y='21' width='16' height='2' /%3E%3C/svg>");
  color: #5eed8e;
}

[data-okay-toggle][aria-expanded="true"]:hover {
  background-color: gray;
}

[data-okay-toggle][aria-hidden] {
  display: none;
}

[data-okay-toggle] > span,
[data-okay-toggle] > span svg {
  display: inline-block;
  vertical-align: middle;
}

[data-okay-toggle] > span {
  font-size: .75em;
}

[data-okay-toggle][aria-expanded="false"] > .menu-toggle-tablet_up-arrow,
[data-okay-toggle][aria-expanded="false"] > .menu-toggle-tablet_text-less,
[data-okay-toggle][aria-expanded="true"] > .menu-toggle-tablet_down-arrow,
[data-okay-toggle][aria-expanded="true"] > .menu-toggle-tablet_text-more {
  display: none;
}

[data-okay-toggle][aria-expanded="true"] > .menu-toggle-tablet_up-arrow,
[data-okay-toggle][aria-expanded="true"] > .menu-toggle-tablet_text-less {
  display: inline-block;
}

[data-okay-overflow] {
  background-color: rgba(191, 191, 191, 0.75);
  position: absolute;
  top: 40px;
  right: 0;
  bottom: 0;
  transform: translateX(0);
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.125);
  list-style: none;
  transition: 400ms transform cubic-bezier(0.55, 0, 0.1, 1);
  visibility: visible;
  z-index: 200;
  text-align: left;
  max-width: 100vw;
  min-width: 75px;
}

.index [data-okay-overflow] {
  top: 70px;
}

[data-okay-overflow][aria-hidden] {
  transform: translateX(100%);
  transition: 400ms transform cubic-bezier(0.55, 0, 0.1, 1), 0s 400ms visibility;
  visibility: hidden;
  position: fixed;
  top: 160px;
}

[data-okay-overflow] > li {
  float: none;
}

[data-okay-overflow] > li a {
  background-color: #595959;
  border-top: 1px solid #666666;
  border-bottom: 1px solid #4d4d4d;
  padding-left: 1.25em;
}

[data-okay-overflow] > li a:hover {
  background-color: #2eb85c;
  border-top-color: #2dd264;
  border-bottom-color: #2d864a;
}

[data-okay-overflow] > .menu-item-current a {
  background-color: #1f1f1f;
  border-top: 1px solid black;
  border-bottom: 1px solid black;
  pointer-events: none;
  cursor: default;
  color: #5eed8e;
}

[data-okay-overflow] > li a [class*="tab"] {
  display: none;
}

.js-nav-fix {
  float: left;
  height: 0;
  margin-top: -12em;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html,
body {
  height: 100%;
  background-color: #1f1f1f;
}

html {
  height: 100%;
  font-size: 16px;
}

body {
  font: 100%/1.5 BlinkMacSystemFont, -apple-system, "Segoe UI", Tahoma, sans-serif;
  box-sizing: border-box;
  color: #fff;
  text-align: center;
  min-height: 100%;
}

body,
.page {
  background-color: inherit;
}

h1 {
  font-size: 2.5em;
  color: #f99371;
}

.index .tiles-section > h2 {
  text-align: right;
  font-size: 1em;
  padding-top: .125em;
  padding-right: 1em;
  padding-left: 1em;
  background-color: #ad9048;
  background-color: #f6d155;
  color: #1f1f1f;
  position: relative;
  height: 30px;
  white-space: nowrap;
  text-overflow: ellipsis;
}

@media all and (min-width: 43.125em) {
  .index .tiles-section > h2 {
    font-size: 1.125em;
    padding-right: 3em;
    padding-top: 0.05em;
  }
}

